<template>
  <div>
    <object id="svgId" :data="svg" type="image/svg+xml"></object>

    <da-dialog :title="dialogTitle" :visible.sync="dialogVisible" @close="closeDialog">
      <da-equal-panel left-width="246px" height="auto">
        <div class="clearfix" slot="left">
          <da-panel :no-head="true" width="100%" height="245px" overflow="auto" padding="0 5px 0 0" padding-content="10px">
            <el-checkbox-group v-model="energyItemIds">
              <div class="text-left energy-item" v-for="(item, index) in currentData" v-if="meterId===item.meterId" :key="index">
                <el-checkbox :label="item.energyItemId">
                  <span class="energy-item-label"> {{item.energyItemName}}</span>
                  <span class="energy-item-data">{{item.data.toFixed(2)}}{{item.unit}}</span>
                </el-checkbox>
              </div>
            </el-checkbox-group>
          </da-panel>
          <div class="pull-right mar-5">
            <el-button type="primary" @click="loadMeterHistoryData()">查询</el-button>
          </div>
        </div>
        <div slot="right">
          <da-panel title="过去24小时趋势图" width="100%" height="auto" padding="0 0 0 5px" padding-content="10px 0 0" ref="panelElectricity">
            <da-line-chart x-axis-format="DD日HH时" :data-lists="historyData" label="energyItemName" value-key="data" unit="unit" x-axis-key="dataTime"></da-line-chart>
          </da-panel>
        </div>
      </da-equal-panel>
      <div slot="footer">
        <el-button @click="dialogVisible=false">关闭</el-button>
      </div>
    </da-dialog>
  </div>
</template>

<script>
import { calcTime } from "@/util/util.js";
import svg from "../../../svg/test3.svg";
export default {
  data() {
    return {
      svg,
      meterIds: "", //表计ID
      energyItemIds: [], //采集项ID（非表计采集项ID）
      currentData: [], //实时数据
      historyData: [], //历史数据
      dialogVisible: false,
      dialogTitle: "",
      meterId: "" //表计ID
    };
  },

  methods: {
    circle_click(obj) {
      console.log(obj);
    },

    // 根据id获取svg内的对象，并可调用dom元素操作；传入callback可对修改内部函数
    getElementFormSvg(id) {
      var object = document.getElementById(id);
      var svgDoc = null;
      try {
        svgDoc = object.contentDocument;
      } catch (e) {
        try {
          svgDoc = object.getSvgElement();
        } catch (e) {
          console.log("svg in object not support in your environment.");
        }
      }
      return svgDoc;
    },
    //查询表计采集项实时数据
    loadMeterCurrentData() {
      let me = this;
      me
        .$ajax({
          url: "rest/analysisData/meterEnergyItemData",
          method: "get",
          params: {
            title: "表计实时数据",
            meterIds: "", //表计ID
            energyItemIds: "", //采集数据项ID
            onLine: "", //是否查询在线数据
            startTime: "", //开始时间
            endTime: "" //结束时间
          }
        })
        .then(res => {
          if (res.data.status) {
            me.currentData = res.data.records;

            if (me.currentData != null && me.currentData.length > 0) {
              let svgDoc = this.getElementFormSvg("svgId");
              for (let i = 0; i < me.currentData.length; i++) {
                this.unit = me.currentData[i].targetUnit;
                //查找数据项并赋值
                let mi = svgDoc.getElementById(
                  "MIID_" + me.currentData[i].meterItemId
                );
                if (mi != null) {
                  mi.textContent =
                    me.currentData[i].abbrName +
                    ":" +
                    me.currentData[i].data.toFixed(2) +
                    " " +
                    me.currentData[i].unit;
                  //c.setAttribute('fill','red');
                }
                //查找表计，添加单击事件
                let m = svgDoc.getElementById(
                  "MID_" + me.currentData[i].meterId
                );
                if (m != null) {
                  m.onclick = () => {
                    me.dialogTitle = me.currentData[i].meterName;
                    me.dialogVisible = true;
                    me.meterId = me.currentData[i].meterId;
                    me.loadMeterHistoryData();
                  };
                }
              }
            }
          } else {
            this.$message.warning(res.data.answerMsg);
          }
        });
    },

    //查询表计采集项最近24小时数据
    loadMeterHistoryData() {
      let me = this;
      let date = new Date();
      date = "2018-07-26 17:00:00"; //////////////////////////////test/////////////////////////
      let endTime = this.$moment(date, "YYYY-MM-DD HH:mm:ss");

      let startTime = calcTime(date, -24, 1, "YYYY-MM-DD HH:mm:ss");
      me
        .$ajax({
          url: "rest/analysisData/meterEnergyItemData",
          method: "get",
          loadingTarget: this.$refs.panelElectricity,
          params: {
            title: "表计最近24小时数据",
            meterIds: me.meterId, //表计ID
            energyItemIds: this.energyItemIds.join(","), //采集数据项ID
            onLine: "", //是否查询在线数据
            startTime: startTime, //开始时间
            endTime: endTime //结束时间
          }
        })
        .then(res => {
          if (res.data.status) {
            me.historyData = res.data.records;
          } else {
            this.$message.warning(res.data.answerMsg);
          }
        });
    },
    closeDialog() {
      this.energyItemIds = [];
    }
  },
  mounted() {
    var object = document.getElementById("svgId");
    object.onload = () => {
      this.loadMeterCurrentData();
    };
  }
};
</script>

<style scoped>
.energy-item {
  margin: 5px 0;
}
.energy-item-label {
  display: inline-block;
  width: 100px;
}
.energy-item-data {
  padding: 3px 0;
  text-align: center;
  display: inline-block;
  background-color: #55dddd;
  color: #fff;
  min-width: 90px;
}
</style>



